import mongoose from 'mongoose';
import Screen from './screen';
let Schema = mongoose.Schema;

let Reserve = new Schema({
  screen: [{
    type: Schema.Types.ObjectId,
    ref: 'Screen'
  }],
  seatInfo: [{
    name: {
    type: String,
    required: true
    },
    seats : {
      noOfSeats: {
        type: Number,
        required: true
      },
      seatsBooked : [{
        type: Number,
        required: true
      }]
    }
  }]
});

module.exports = mongoose.model('Reserve', Reserve);
